.productListTopButtonsH2{
    position:relative;
    display:flex;
    justify-content: center;
    justify-items: center;
    align-items: center;

    margin:0;
}

.listButtons {
    position: sticky;
    top: 0.5vh;
    display: flex;
    height:8.5vh;
    justify-content: center; /* Wyrównuje elementy do początku */
    margin: 0.5vh 0.2vw 0.5vh;
    text-align: center;
    background: transparent;
}

.listButtons div {
    flex: 1;  /* Po połowie dostępnej przestrzeni dla każdego przycisku */
    height: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    justify-content: center;
    background: transparent;
}

.leftButtonDiv {
    position:relative;
    display:flex;
    border-radius: 20px;
    height:100%;
    background-color: #4caf50;
    text-align: center;
    color: white;
    margin-right: 7px;

    /* Zapewnienie, że nie ma marginesów, które mogą wpływać na wysokość */
}

.rightButtonDiv {
    position:relative;
    display:flex;
    background-color: #4caf50;
    text-align: center;
    color: white;
    height:100%;
    border-radius: 20px;
}

.leftButton {
    position:relative;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    justify-items: center;
    justify-content: center;
    display:flex;
    align-items: center;

}

.rightButton {
    border-radius: 20px;
    width: 100%;
    height: 100%;
    justify-items: center;
    justify-content: center;
    display:flex;
    align-items: center;

    margin: 0;
    padding: 0;
}
.leftButton.active, .rightButton.active {
    background-color: #4caf50; /* Kolor tła dla przycisku "Aktualne" */
    color: white; /* Kolor tekstu dla przycisku "Aktualne" */
}

/* Możesz również dodać kolory tła dla przycisków, które nie są aktywne */
.leftButton:not(.active), .rightButton:not(.active) {
    background-color: #7a7e96; /* Kolor tła dla przycisku "Stare" */
    color: black; /* Kolor tekstu dla przycisku "Stare" */
}
.leftButton:not(.active):hover, .rightButton:not(.active):hover{
    background-color: #4caf50;

}
.settings-container{

}
.settings-container-otherSites {
    position: absolute;
    left: 0;
    transform: translateX(-100%); /* Schowany domyślnie */
    transition: transform 0.5s ease-in-out;
    width: 15vw; /* Stała szerokość */
    height: 100%;
    z-index: 111;
}

.settings-container-otherSites.open {
    transform: translateX(0); /* Wysunięty */
}

.settings-button {
    position: absolute; /* Zmień na absolute */
    left: 100%; /* Ustawienie przycisku bezpośrednio po prawej stronie kontenera */
    top:50%;
    transform: translateY(-50%);
    height: 3vh;
    width: 3vw;
    z-index: 111;
}

.settingBurgerButton{
    width:100%;
    height:100%;
}


.productList {
    position:relative;
    display: flex;
    flex-wrap: wrap;
    width: 100%; /* Zajmowanie całej dostępnej szerokości */

    overflow-y: auto;
    justify-content: center; /* Wyrównanie do początku */
    align-content: flex-start; /* Ustawienie rzędów na początku */

    margin:auto;
}

@media only screen and (max-width: 1600px) {
    .leftButtonDiv {
        margin-right: 5px;
    }
}

@media only screen and (max-width: 900px) {


    .leftButton {
        border-radius: 10px;

    }

    .rightButton {
        border-radius: 10px;

    }

    .leftButtonDiv {
        margin-right: 3px;
    }
}

@media only screen and (max-width: 600px) {
    .leftButtonDiv {
        margin-right: 2px;
    }
}
